<template>
  <AppBorder :app-info="Crontab" :size="size" herder-css="header1">
    <template v-slot:header>
      <div class="fx-ac">
        <span>{{ Crontab.icon }}ÂÆöÊó∂‰ªªÂä°</span>
      </div>
    </template>
    <template v-slot:main>
      <div class="fx-ver CrontabBox" @mousemove="(ev)=>{ev.stopPropagation()}">
        <div class="fx-pj title">
          <span></span>
          <button class="fz26" @click="taskList">‰ªªÂä°ÂàóË°®</button>
          <button class="fz26" @click="curPage=2">ËÆæÁΩÆ‰ªªÂä°</button>
          <button class="fz26" @click="curPage=3">ÂÆöÊó∂Ê∂àÊÅØ</button>
          <span></span>
        </div>
        <div v-show="curPage===1">
          <el-table
              :data="tableData"
              max-height="600"
              border
              style="width: 100%">
            <el-table-column
                v-for="(col,idx) of tableCol"
                :key=idx
                :prop=col.prop
                :label=col.label
                :width=col.width>
            </el-table-column>
            <el-table-column
                prop=""
                label="Êìç‰Ωú"
                width="200">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click="cancel(scope.row.id)">ÂÖ≥Èó≠‰ªªÂä°</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div v-show="curPage===2">
          <el-form :model="ruleForm" status-icon :rules="rules" ref="schedule" label-width="120px"
                   class="demo-ruleForm fx-ver fx-pac">
            <el-form-item :label="val" :prop="key" v-for="(val,key,idx) in formItem" :key="idx">
              <el-input type="text" v-model="ruleForm[key]" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item class="">
              <el-button type="primary" @click="submitForm('schedule')">Êèê‰∫§</el-button>
              <el-button @click="resetForm('schedule')">ÈáçÁΩÆ</el-button>
            </el-form-item>
          </el-form>
        </div>
        <div v-show="curPage===3" class="fx-ver fx-pac">
          <el-form :model="notifyForm" status-icon :rules="rules2" ref="notify" label-width="120px"
                   class="demo-ruleForm fx-ver fx-pac">
            <el-form-item :label="val" :prop="key" v-for="(val,key,idx) in formItem2" :key="idx">
              <el-input type="text" v-model="notifyForm[key]" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item class="">
              <el-button type="primary" @click="submitForm('notify')">Êèê‰∫§</el-button>
              <el-button @click="resetForm('notify')">ÈáçÁΩÆ</el-button>
            </el-form-item>
          </el-form>
          <button @click="gotoHelper">->ÂâçÂæÄCronÂú®Á∫øË°®ËææÂºèÁîüÊàêÂô®</button>
        </div>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from '../AppBorder'
import pageManager from "@/utils/PageManager";

export default {
  name: "Crontab",
  components: {AppBorder},
  data() {
    return {
      Crontab: {
        name: "Crontab",
        isLoad: false,
        min: true,
        icon: '‚è±Ô∏è'
      },
      size: {
        width: '1200px',
        height: '600px'
      },
      ruleForm: {
        serviceName: '',
        methodName: '',
        cron: '',
        paramsName: '',
        params: ''
      },
      notifyForm: {
        qq: '',
        email: '',
        cron: '',
        content: ''
      },
      formItem: {
        serviceName: 'ÊúçÂä°Âêç',
        methodName: 'ÊñπÊ≥ïÂêç',
        cron: 'CronË°®ËææÂºè',
        paramsName: 'ÂèÇÊï∞ÂÖ®Á±ªÂêçÂàóË°®',
        params: 'ÂèÇÊï∞ÂÄº'
      },
      formItem2: {
        qq: 'qqÂè∑',
        email: 'ÈÇÆÁÆ±',
        cron: 'CronË°®ËææÂºè',
        content: 'Ê∂àÊÅØÂÜÖÂÆπ'
      },
      rules: {
        serviceName: [
          {required: true, message: 'ËØ∑ËæìÂÖ•Ë∞ÉÁî®ÊúçÂä°Âêç', trigger: 'blur'}
        ],
        methodName: [
          {required: true, message: 'ËØ∑ËæìÂÖ•Ë∞ÉÁî®ÊñπÊ≥ïÂêç', trigger: 'blur'}
        ],
        cron: [
          {required: true, message: 'ËØ∑ËæìÂÖ•CronË°®ËææÂºè', trigger: 'blur'}
        ]
      },
      rules2: {
        qq: [
          {required: true, message: 'ËØ∑ËæìÂÖ•qqÂè∑', trigger: 'blur'}
        ],
        email: [
          {required: true, message: 'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±', trigger: 'blur'}
        ],
        cron: [
          {required: true, message: 'ËØ∑ËæìÂÖ•CronË°®ËææÂºè', trigger: 'blur'}
        ],
        content: [
          {required: true, message: 'ËØ∑ËæìÂÖ•Ê∂àÊÅØÂÜÖÂÆπ', trigger: 'blur'}
        ]
      },
      tableData: [],
      curPage: 1,
      tableCol: [
        {prop: "id", label: "id", width: 180},
        {prop: "serviceName", label: "ÊúçÂä°Âêç", width: 180},
        {prop: "methodName", label: "ÊñπÊ≥ïÂêç", width: 120},
        {prop: "params", label: "ÂèÇÊï∞", width: 150},
        {prop: "cron", label: "Corn", width: 120},
        {prop: "runCount", label: "Â∑≤ÊâßË°åÊ¨°Êï∞", width: 70},
      ]
    }
  },
  created() {
    this.getTaskList();
  },
  methods: {
    getTaskList() {
      this.$http.get("/api/getTaskList")
          .then(ret => {
            if (ret.code === 200) {
              ret.data.forEach(v => {
                v.params && (v.params = v.params.join(","));
                v.paramsName && (v.paramsName = v.paramsName.join(","));
              })
              this.tableData = ret.data;
            } else
              this.$message.error(ret.msg)
          })
    },
    cancel(id) {
      this.$http.get("/api/cancelTask", {id})
          .then(ret => {
            if (ret.code === 200) {
              this.$message.success(ret.data);
              this.getTaskList();
            } else
              this.$message.error(ret.msg)
          }).catch(err => {
        this.$message.error(err);
      })
    },
    taskList() {
      this.curPage = 1;
      this.getTaskList();
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    setSchedule() {
      let paramsName = this.ruleForm.paramsName.split(',');
      let params = this.ruleForm.params.split(",");
      let param = {};
      if (paramsName[0] === '' || params[0] === '') {
        param.serviceName = this.ruleForm.serviceName;
        param.methodName = this.ruleForm.methodName;
        param.cron = this.ruleForm.cron;
      } else param = {...this.ruleForm, paramsName, params}
      this.$http.postBody('/api/setSchedule', param
      ).then(ret => {
        if (ret.code === 200) {
          console.log(ret)
          this.$message.success("‰ªªÂä°ÈÖçÁΩÆÊàêÂäü" + ret.data);
        } else
          this.$message.error(ret.msg)
      }).catch(err => {
        this.$message.error(err);
      });
    },
    setNotify(){
      this.$http.postBody('/api/notifyByTwoWay', this.notifyForm
      ).then(ret => {
        if (ret.code === 200) {
          console.log(ret)
          this.$message.success("‰ªªÂä°ÈÖçÁΩÆÊàêÂäü" + ret.data);
        } else
          this.$message.error(ret.msg)
      }).catch(err => {
        this.$message.error(err);
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if ('schedule' === formName) this.setSchedule();
          if ('notify'=== formName) this.setNotify();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    gotoHelper(){
      pageManager.search("http://cron.ciding.cc/");
    },
    closeApp() {
      this.$router.push("/desktop");
    }
  }
}
</script>

<style scoped>
.title {
  margin: 20px 0;
}

.CrontabBox {
  background-color: white;
  width: 99.8%;
  height: 99%;
  border: 1px saddlebrown solid;
}
</style>
